import { useState } from 'react';
import { __ } from '@wordpress/i18n';
import { withSelect } from '@wordpress/data';
import { AlignmentToolbar, BlockControls, InspectorControls } from '@wordpress/block-editor';
import { PanelBody, PanelRow, RangeControl, Button, ToolbarGroup, ToolbarButton, Dashicon, TextControl, TextareaControl, ToggleControl, TabPanel, __experimentalAlignmentMatrixControl as AlignmentMatrixControl, __experimentalUnitControl as UnitControl } from '@wordpress/components';
import produce from 'immer';

// Settings Components
import { Label, Background, BColor, BDevice, BorderControl, ColorsControl, HelpPanel, SpaceControl, Typography } from '../../Components';
import { AboutPro, FrontShortCode, SelectControlPro } from '../../Components/Pro';
import { gearIcon } from '../../Components/utils/icons';
import { pxUnit, perUnit, emUnit, vhUnit } from '../../Components/utils/options';

import { BControlPro } from '../bplugins_sdk/src/components/v1';
import options from './utils/options';
const { generalStyleTabs, effects } = options;

const Settings = ({ attributes, setAttributes, setActiveIndex, currentPostId }) => {
	const { isPremium, slides = [], columns, columnGap, sliderWidth, sliderHeight, isLoop, isTouchMove, speed, isAutoplay, autoplayOptions = {}, freeModeOptions = {}, effect, keyboardOptions = {}, isMousewheel = false, isPage, pageOnDevice = {}, isPageClickable, isPageDynamic, isPrevNext, prevNextOnDevice = {}, sliderBG = {}, sliderPadding = {}, pageColor, pageWidth, pageHeight, pageBorder, prevNextColor, sliderAlign, isTitle, titleTypo, isDesc, descTypo, isBtn, linkTarget, btnTypo, btnPadding, btnBorder } = attributes;

	const [device, setDevice] = useState('desktop');
	const [aboutProOpen, setAboutProOpen] = useState(false);

	const addSlide = () => {
		setAttributes({
			slides: [...slides, {
				background: { color: '#00000080' },
				border: {},
				position: 'center center',
				positionHolder: 'center center',
				childPositions: [{ 'top': 11, 'left': 5.8 }, { 'top': 22, 'left': 5.8 }, { 'top': 33, 'left': 5.8 }],
				title: `Slide Title ${slides.length + 1}`,
				titleColor: '#fff',
				description: `This content area describes slider ${slides.length + 1} descriptions/details.`,
				descColor: '#fff',
				btnText: `Button ${slides.length + 1}`,
				btnLink: '#',
				btnColors: { color: '#fff', bg: '#4527a4' },
				btnHovColors: { color: '#fff', bg: '#8344c5' }
			}]
		});
		setActiveIndex(slides.length);
	}

	const updateSlide = (index, type, val) => {
		const newSlides = [...slides];
		newSlides[index][type] = val;
		setAttributes({ slides: newSlides });
		setActiveIndex(index);
	}

	const premiumProps = {
		isPremium,
		setOpen: setAboutProOpen
	};

	const panelBodyIF = {
		className: 'bPlPanelBody',
		initialOpen: false
	}

	return <>
		<InspectorControls>
			<div className='csbInspectorInfo'>
				{!isPremium && <span>Need more block like this? Checkout the bundle ➡ <a href='https://wordpress.org/plugins/b-blocks' target='_blank' rel='noopener noreferrer'>B Blocks</a></span>}

				{isPremium && <FrontShortCode postType='csb' shortCode={`[csb id=${currentPostId}]`} />}
			</div>

			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs}>{tab => <>
				{'general' === tab.name && <>
					<HelpPanel slug='content-slider-block' docsLink='https://csb.bplugins.com/docs' />


					<PanelBody className='bPlPanelBody addRemoveItems' title={__('Add or Remove Slides', 'content-slider-block')}>
						{slides.map((item, index) => {
							const { background, border = {}, title, titleColor, description, descColor, btnText, btnLink, btnColors, btnHovColors } = item;

							return <PanelBody key={index} className='bPlPanelBody editItem' title={__(`Slide No ${index + 1}:`, 'content-slider-block')} initialOpen={0 !== index ? false : true}>
								<Background label={__('Background', 'content-slider-block')} value={background} onChange={val => updateSlide(index, 'background', val)} defaults={{ color: '#00000080' }} />

								<BControlPro label={__('Border:', 'content-slider-block')} value={border} onChange={val => updateSlide(index, 'border', val)}  {...premiumProps} Component={BorderControl} />

								<PanelRow>
									<Label className=''>{__('Content Position', 'content-slider-block')}</Label>
									<AlignmentMatrixControl onChange={val => updateSlide(index, 'position', val)} />
								</PanelRow>
								<small>{__('You can also change the content position by dragging element.', 'content-slider-block')}</small>

								<PanelRow className='mt20'>
									<Label className=''>{__('Title:', 'content-slider-block')}</Label>
									<TextControl value={title} onChange={val => updateSlide(index, 'title', val)} placeholder={__('Slide title', 'content-slider-block')} />
								</PanelRow>

								<BColor label={__('Title Color:', 'content-slider-block')} value={titleColor} onChange={val => updateSlide(index, 'titleColor', val)} defaultColor='#fff' />

								<Label>{__('Description:', 'content-slider-block')}</Label>
								<TextareaControl value={description} onChange={val => updateSlide(index, 'description', val)} placeholder={__('Description of the slider', 'content-slider-block')} />

								<BColor label={__('Description Color:', 'content-slider-block')} value={descColor} onChange={val => updateSlide(index, 'descColor', val)} defaultColor='#fff' />

								<PanelRow className='mt20'>
									<Label className=''>{__('Button Text:', 'content-slider-block')}</Label>
									<TextControl value={btnText} onChange={val => updateSlide(index, 'btnText', val)} placeholder={__('Button label', 'content-slider-block')} />
								</PanelRow>

								<PanelRow>
									<Label className=''>{__('Button Link:', 'content-slider-block')}</Label>
									<TextControl value={btnLink} onChange={val => updateSlide(index, 'btnLink', val)} placeholder={__('Button link', 'content-slider-block')} />
								</PanelRow>

								<ColorsControl label={__('Button Colors:', 'content-slider-block')} value={btnColors} onChange={val => updateSlide(index, 'btnColors', val)} defaults={{ color: '#fff', bg: '#4527a4' }} />

								<ColorsControl label={__('Button Hover Colors:', 'content-slider-block')} value={btnHovColors} onChange={val => updateSlide(index, 'btnHovColors', val)} defaults={{ color: '#fff', bg: '#8344c5' }} />

								<PanelRow className='itemAction mt20'>
									<Button className='removeItem' label={__('Remove', 'content-slider-block')} onClick={e => {
										e.preventDefault();
										setAttributes({ slides: [...slides.slice(0, index), ...slides.slice(index + 1)] });
									}}><Dashicon icon='no' />{__('Remove', 'content-slider-block')}</Button>

									<Button className='duplicateItem' label={__('Duplicate', 'content-slider-block')} onClick={e => {
										e.preventDefault();
										setAttributes({ slides: [...slides, { ...slides[index] }] });
									}}>{gearIcon}{__('Duplicate', 'content-slider-block')}</Button>
								</PanelRow>
							</PanelBody>
						})}

						<div className='addItem'>
							<Button label={__('Add New Slide', 'content-slider-block')} onClick={addSlide}><Dashicon icon='plus' />{__('Add New Slide', 'content-slider-block')}</Button>
						</div>
					</PanelBody>


					<PanelBody title={__('Layout Settings', 'content-slider-block')} {...panelBodyIF}>
						<PanelRow>
							<Label className='mb5'>{__('Columns:', 'content-slider-block')}</Label>
							<BDevice device={device} onChange={val => setDevice(val)} />
						</PanelRow>
						<RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' />

						<Label>{__('Column Gap:', 'content-slider-block')}</Label>
						<RangeControl value={columnGap} onChange={val => setAttributes({ columnGap: val })} min={0} max={250} step={1} beforeIcon='arrow-right-alt' />

						<UnitControl className='mt20' label={__('Width:', 'content-slider-block')} labelPosition='left' value={sliderWidth} onChange={val => setAttributes({ sliderWidth: val })} units={[pxUnit(), perUnit(), emUnit()]} />

						<UnitControl className='mt20' label={__('Height:', 'content-slider-block')} labelPosition='left' value={sliderHeight} onChange={val => setAttributes({ sliderHeight: val })} units={[pxUnit(), emUnit(), vhUnit()]} />
					</PanelBody>
				</>}


				{'options' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Basic Options', 'content-slider-block')}>
						<ToggleControl label={__('Enable Loop', 'content-slider-block')} checked={isLoop} onChange={val => setAttributes({ isLoop: val })} />

						<ToggleControl className='mt10' label={__('Enable Touch Move', 'content-slider-block')} checked={isTouchMove} onChange={val => setAttributes({ isTouchMove: val })} />
						<small>{__('Switch slide with grab in anywhere in slide', 'content-slider-block')}</small>
						<small>{__('Touch Move will not work in backend', 'content-slider-block')}</small>

						<Label>{__('Speed (s):', 'content-slider-block')}</Label>
						<RangeControl value={speed} onChange={val => setAttributes({ speed: val })} min={0} max={10} step={.05} />
						<small>{__('Smaller speed value will be slide faster', 'content-slider-block')}</small>
					</PanelBody>


					<PanelBody title={__('Autoplay', 'content-slider-block')} {...panelBodyIF}>
						<ToggleControl label={__('Enable Autoplay', 'content-slider-block')} checked={isAutoplay} onChange={val => setAttributes({ isAutoplay: val })} />
						<small>{__('Autoplay will not work in backend', 'content-slider-block')}</small>

						{isAutoplay && <>
							<BControlPro className='mt20' label={__('Delay (s):', 'content-slider-block')} value={autoplayOptions.delay} onChange={val => setAttributes({ autoplayOptions: { ...autoplayOptions, delay: val } })} min={0} max={10} step={.05} beforeIcon='visibility' {...premiumProps} Component={RangeControl} />
							<small>{__('Smaller delay value will be autoplay faster', 'content-slider-block')}</small>

							<BControlPro className='mt20' label={__('Disable on Interaction', 'content-slider-block')} checked={autoplayOptions.disableOnInteraction} onChange={val => setAttributes({ autoplayOptions: { ...autoplayOptions, disableOnInteraction: val } })} {...premiumProps} Component={ToggleControl} />

							<BControlPro className='mt10' label={__('Pause on Mouse Enter', 'content-slider-block')} checked={autoplayOptions.pauseOnMouseEnter} onChange={val => setAttributes({ autoplayOptions: { ...autoplayOptions, pauseOnMouseEnter: val } })} {...premiumProps} Component={ToggleControl} />
							<small>{__(`If 'Disable on Interaction' is also enabled, it will stop autoplay instead of pause`, 'content-slider-block')}</small>

							<BControlPro className='mt10' label={__('Reverse Direction', 'content-slider-block')} checked={autoplayOptions.reverseDirection} onChange={val => setAttributes({ autoplayOptions: { ...autoplayOptions, reverseDirection: val } })} {...premiumProps} Component={ToggleControl} />

							<BControlPro className='mt10' label={__('Stop on Last Slide', 'content-slider-block')} checked={autoplayOptions.stopOnLastSlide} onChange={val => setAttributes({ autoplayOptions: { ...autoplayOptions, stopOnLastSlide: val } })} {...premiumProps} Component={ToggleControl} />
						</>}
					</PanelBody>


					<PanelBody title={__('Free Mode', 'content-slider-block')} {...panelBodyIF}>
						<BControlPro label={__('Enable Free Mode', 'content-slider-block')} checked={freeModeOptions.enabled} onChange={val => setAttributes({ freeModeOptions: { ...freeModeOptions, enabled: val } })} {...premiumProps} Component={ToggleControl} />
						<small>{__('Smooth Scrolling with dragging the slide', 'content-slider-block')}</small>
						<br />
						<small>{__('Enable Touch Move to use Free Mode', 'content-slider-block')}</small>

						{freeModeOptions.enabled && <BControlPro className='mt10' label={__('Sticky', 'content-slider-block')} checked={freeModeOptions.sticky} onChange={val => setAttributes({ freeModeOptions: { ...freeModeOptions, sticky: val } })} {...premiumProps} Component={ToggleControl} />}
					</PanelBody>


					<PanelBody title={__('Effects', 'content-slider-block')} {...panelBodyIF}>
						<PanelRow>
							<Label className=''>{__('Effect:', 'content-slider-block')}</Label>
							<SelectControlPro value={effect}
								onChange={val => {
									setAttributes({ effect: val });
									val === 'slide' && setAttributes({
										columns: { desktop: 1, tablet: 1, mobile: 1 },
										sliderWidth: '100%',
										sliderHeight: '400px',
										sliderPadding: { vertical: '0px', horizontal: '0px' }
									});
									val === 'fade' && setAttributes({
										columns: { desktop: 1, tablet: 1, mobile: 1 },
										sliderWidth: '100%',
										sliderHeight: '400px',
										sliderPadding: { vertical: '0px', horizontal: '0px' }
									});
									val === 'cube' && setAttributes({
										columns: { desktop: 1, tablet: 1, mobile: 1 },
										sliderWidth: '450px',
										sliderHeight: '500px',
										sliderPadding: { vertical: '50px', horizontal: '50px' }
									});
									val === 'creative' && setAttributes({
										columns: { desktop: 1, tablet: 1, mobile: 1 },
										sliderWidth: '100%',
										sliderHeight: '400px',
										sliderPadding: { vertical: '0px', horizontal: '0px' }
									});
									val === 'coverflow' && setAttributes({
										columns: { desktop: 3, tablet: 2, mobile: 1 },
										sliderWidth: '100%',
										sliderHeight: '400px',
										sliderPadding: { vertical: '0px', horizontal: '0px' }
									});
									val === 'flip' && setAttributes({
										columns: { desktop: 1, tablet: 1, mobile: 1 },
										sliderWidth: '450px',
										sliderHeight: '500px',
										sliderPadding: { vertical: '50px', horizontal: '50px' }
									});
									val === 'cards' && setAttributes({
										columns: { desktop: 1, tablet: 1, mobile: 1 },
										sliderWidth: '400px',
										sliderHeight: '500px',
										sliderPadding: { vertical: '50px', horizontal: '50px' }
									});
								}}
								options={effects}
								proValues={['cube', 'coverflow', 'flip', 'cards']}
								{...premiumProps}
							/>
						</PanelRow>
						<small>{__('To work fade, cube, creative, flip & cards effects properly, set single column per view.', 'content-slider-block')}</small>
						<br />
						<small>{__('Some settings may change when effect will be changed.', 'content-slider-block')}</small>
					</PanelBody>


					<PanelBody title={__('Keyboard Control', 'content-slider-block')} {...panelBodyIF}>
						<BControlPro label={__('Enable Keyboard Control', 'content-slider-block')} checked={keyboardOptions.enabled} onChange={val => setAttributes({ keyboardOptions: { ...keyboardOptions, enabled: val } })}{...premiumProps} Component={ToggleControl} />
					</PanelBody>


					<PanelBody title={__('Mousewheel', 'content-slider-block')} {...panelBodyIF}>
						<BControlPro label={__('Enable Slide on Mousewheel', 'content-slider-block')} checked={isMousewheel} onChange={val => setAttributes({ isMousewheel: val })}{...premiumProps} Component={ToggleControl} />
					</PanelBody>


					<PanelBody title={__('Pagination', 'content-slider-block')} {...panelBodyIF}>
						<ToggleControl label={__('Show Pagination', 'content-slider-block')} checked={isPage} onChange={val => setAttributes({ isPage: val })} />

						{isPage && <>
							<BControlPro className='mt10' label={__('Show On Tablet', 'content-slider-block')} checked={pageOnDevice?.tablet} onChange={val => setAttributes({ pageOnDevice: { ...pageOnDevice, tablet: val } })} {...premiumProps} Component={ToggleControl} />

							<BControlPro className='mt10' label={__('Show On Mobile', 'content-slider-block')} checked={pageOnDevice?.mobile} onChange={val => setAttributes({ pageOnDevice: { ...pageOnDevice, mobile: val } })} {...premiumProps} Component={ToggleControl} />

							<ToggleControl className='mt10' label={__('Enable Pagination Clickable', 'content-slider-block')} checked={isPageClickable} onChange={val => setAttributes({ isPageClickable: val })} />

							<ToggleControl className='mt10' label={__('Enable Pagination Dynamic Bullets', 'content-slider-block')} checked={isPageDynamic} onChange={val => setAttributes({ isPageDynamic: val })} />
						</>}
					</PanelBody>


					<PanelBody title={__('Navigation', 'content-slider-block')} {...panelBodyIF}>
						<ToggleControl label={__('Show Preview Next Button', 'content-slider-block')} checked={isPrevNext} onChange={val => setAttributes({ isPrevNext: val })} />

						{isPrevNext && <>
							<BControlPro className='mt10' label={__('Show On Tablet', 'content-slider-block')} checked={prevNextOnDevice?.tablet} onChange={val => setAttributes({ prevNextOnDevice: { ...prevNextOnDevice, tablet: val } })} {...premiumProps} Component={ToggleControl} />

							<BControlPro className='mt10' label={__('Show On Mobile', 'content-slider-block')} checked={prevNextOnDevice?.mobile} onChange={val => setAttributes({ prevNextOnDevice: { ...prevNextOnDevice, mobile: val } })} {...premiumProps} Component={ToggleControl} />
						</>}
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Slider', 'content-slider-block')}>
						<BControlPro label={__('Background', 'content-slider-block')} value={sliderBG} onChange={val => setAttributes({ sliderBG: val })} defaults={{ color: '#0000' }} {...premiumProps} Component={Background} />

						<BControlPro className='mt20' label={__('Padding:', 'content-slider-block')} value={sliderPadding} onChange={val => setAttributes({ sliderPadding: val })} defaults={{ vertical: '0px', horizontal: '0px' }} {...premiumProps} Component={SpaceControl} />
					</PanelBody>


					{isPage || isPrevNext ? <PanelBody title={__('Options', 'content-slider-block')} {...panelBodyIF}>
						{isPage && <>
							<BColor label={__('Pagination Bullets Color:', 'content-slider-block')} value={pageColor} onChange={val => setAttributes({ pageColor: val })} defaultColor='#fff' />

							<UnitControl className='mt20' label={__('Pagination Width:', 'content-slider-block')} labelPosition='left' value={pageWidth} onChange={val => setAttributes({ pageWidth: val })} units={[pxUnit(), emUnit()]} />

							<UnitControl className='mt20' label={__('Pagination Height:', 'content-slider-block')} labelPosition='left' value={pageHeight} onChange={val => setAttributes({ pageHeight: val })} units={[pxUnit(), emUnit()]} />

							<BorderControl label={__('Pagination Border:', 'content-slider-block')} value={pageBorder} onChange={val => setAttributes({ pageBorder: val })} defaults={{ radius: '50%' }} />
						</>}

						{isPrevNext && <BColor label={__('Preview Next Button Color:', 'content-slider-block')} value={prevNextColor} onChange={val => setAttributes({ prevNextColor: val })} defaultColor='#fff' />}
					</PanelBody> : ''}


					<PanelBody title={__('Slide Title', 'content-slider-block')} {...panelBodyIF}>
						<ToggleControl label={__('Show Title', 'content-slider-block')} checked={isTitle} onChange={val => setAttributes({ isTitle: val })} />

						{isTitle && <Typography value={titleTypo} onChange={val => setAttributes({ titleTypo: val })} defaults={{ fontSize: { desktop: 25, tablet: 22, mobile: 20 } }} produce={produce} />}
					</PanelBody>


					<PanelBody title={__('Slide Description', 'content-slider-block')} {...panelBodyIF}>
						<ToggleControl label={__('Show Description', 'content-slider-block')} checked={isDesc} onChange={val => setAttributes({ isDesc: val })} />

						{isDesc && <Typography value={descTypo} onChange={val => setAttributes({ descTypo: val })} defaults={{ fontSize: { desktop: 15, tablet: 15, mobile: 15 } }} produce={produce} />}
					</PanelBody>


					<PanelBody title={__('Slide Button', 'content-slider-block')} {...panelBodyIF}>
						<ToggleControl label={__('Show Button', 'content-slider-block')} checked={isBtn} onChange={val => setAttributes({ isBtn: val })} />

						{isBtn && <>
							<ToggleControl className='mt10' label={__('Open link in new tab', 'content-slider-block')} checked={'_blank' === linkTarget ? true : false} onChange={val => setAttributes({ linkTarget: val ? '_blank' : '' })} />

							<Typography value={btnTypo} onChange={val => setAttributes({ btnTypo: val })} defaults={{ fontSize: { desktop: 16, tablet: 16, mobile: 16 } }} produce={produce} />

							<SpaceControl className='mt20' label={__('Padding:', 'content-slider-block')} value={btnPadding} onChange={val => setAttributes({ btnPadding: val })} defaults={{ vertical: '12px', horizontal: '35px' }} />

							<BorderControl label={__('Border:', 'content-slider-block')} value={btnBorder} onChange={val => setAttributes({ btnBorder: val })} defaults={{ radius: '3px' }} />
						</>}
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<ToolbarGroup className='bPlToolbar'>
				<ToolbarButton label={__('Add New Slide', 'content-slider-block')} onClick={addSlide} ><Dashicon icon='plus' /></ToolbarButton>
			</ToolbarGroup>

			<AlignmentToolbar value={sliderAlign} onChange={val => setAttributes({ sliderAlign: val })} describedBy={__('Slider Alignment')} alignmentControls={[
				{ title: __('Slider in left', 'content-slider-block'), align: 'left', icon: 'align-left' },
				{ title: __('Slider in center', 'content-slider-block'), align: 'center', icon: 'align-center' },
				{ title: __('Slider in right', 'content-slider-block'), align: 'right', icon: 'align-right' }
			]} />
		</BlockControls>


		<AboutPro aboutProOpen={aboutProOpen} setAboutProOpen={setAboutProOpen} link='https://bplugins.com/products/content-slider-block/#pricing'>
			<li>&emsp;<strong>{__('Border: ', 'content-slider-block')}</strong>{__('Border style options in every single slide.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Delay time: ', 'content-slider-block')}</strong>{__('Delay time option in autoplay.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Disable on Interaction: ', 'content-slider-block')}</strong>{__('Disable on Interaction option in autoplay.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Reverse Direction: ', 'content-slider-block')}</strong>{__('Reverse Direction option in autoplay.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Pause on Mouse Enter: ', 'content-slider-block')}</strong>{__('Pause on Mouse Enter option in autoplay.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Stop on Last Slide: ', 'content-slider-block')}</strong>{__('Stop on Last Slide option in autoplay.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Free Mode: ', 'content-slider-block')}</strong>{__('Free Mode option on drag slide.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Sticky: ', 'content-slider-block')}</strong>{__('Sticky option in free mode.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Effects: ', 'content-slider-block')}</strong>{__('Explore more effects such as Cube, Coverflow, Flip, and Cards.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Keyboard Control: ', 'content-slider-block')}</strong>{__('Slide with Keyboard control.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Slide on Mousewheel: ', 'content-slider-block')}</strong>{__('Slide on Mousewheel option.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Pagination Device: ', 'content-slider-block')}</strong>{__('Show/Hide pagination in different devices(tablet and mobile).', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Navigation Device: ', 'content-slider-block')}</strong>{__('Show/Hide navigation in different devices(tablet and mobile).', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Background: ', 'content-slider-block')}</strong>{__('Background style option for slider wrapper.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Padding: ', 'content-slider-block')}</strong>{__('Padding style option for slider wrapper.', 'content-slider-block')}</li>

			<li>&emsp;<strong>{__('Shortcode: ', 'content-slider-block')}</strong>{__('Shortcode option to use anywhere.', 'content-slider-block')}</li>
		</AboutPro>
	</>;
};
export default withSelect((select) => {
	return {
		currentPostId: select('core/editor').getCurrentPostId()
	};
})(Settings);